<template>
    <div class="container">
        <h3>新巴/城巴 路線搜尋</h3>
        <div class="container-fluid my-1">
            <div class="row">
                <div class="col mx-0 mp-0">
                    <b-form-input v-model="route" placeholder="請輸入路線" id="route_input" @input="v => { route = v.toUpperCase() }"></b-form-input>
                </div>
                <div class="col col-btn-div p-0">
                    <b-button type="button" class="btn col-btn mx-1 p-0" id="clear_btn" @click="clearRouteInput()"><img src="@/assets/cancel.png" class="btn-img m-0 p-0" /></b-button>
                    <b-button type="button" class="btn btn-success col-btn mx-1 p-0" id="refresh_btn" @click="reloadPage()"><img src="@/assets/refresh.png" class="btn-img m-0 p-0" /></b-button>
                </div>
            </div>
        </div>
        
        <div class="route-list">
            <table class="table">
                <thead>
                    <tr>
                        <th>路線</th>
                        <th>方向</th>
                    </tr>
                </thead>
                <tbody>
                    <router-link v-for="route in search_route_list" :key="route.id" tag="tr" :to="route_link(route.route)">
                        <td>{{ route.route }}</td>
                        <td>{{ route.orig_tc + " -> " + route.dest_tc }}</td>
                    </router-link>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import * as util from '../util/util'

export default {
    name: 'NWFBPage',
    data(){
        return {
            route: '',
            route_list: [],
            company: 'NWFB',
        }
    },
    computed: {
        search_route_list: function() {
            if (this.route == ''){
                return this.route_list;
            }
            else {
                return this.route_list.filter(route_item => route_item.route.indexOf(this.route) == 0);
            }
        }
    },
    mounted() {
        (async () => {
            let promises = [util.getNWFBCTBRouteList('NWFB'), util.getNWFBCTBRouteList('CTB')];
            await Promise.all(promises);

            this.loadRoute();
        })();
    },
    methods: {
        loadRoute: function (){
            let NWFBRouteList = JSON.parse(localStorage.getItem("NWFBRouteList"));
            let CTBRouteList = JSON.parse(localStorage.getItem("CTBRouteList"));

            this.route_list = NWFBRouteList.concat(CTBRouteList);
            // this.route_list.sort(function(a,b){
            //     let route_1 = a.route.toString();
            //     let route_2 = b.route.toString();
            //     return route_1 - route_2;
            //     //return a.route - b.route;
            // });
            console.log(this.route_list);
            // may perform other sorting
        },
        clearRouteInput: function (){
            console.log("clearButtonCLick");
            this.route = '';
        },
        reloadPage: function (){
            location.reload();
        },

        route_link: function(route){
            return '/Bravobus/'+ this.company + '/' + route + '/outbound';
        }
    },
}

</script>

<style scoped>
.col-btn-div {
    -ms-flex: 0 0 100px;
    flex: 0 0 100px;

}
.col-btn {
    width: 38px;
    height: 38px;
}

.btn-img {
    width: 30px;
}
</style>
